<template>
  <section class="todoapp">
    <!-- 头部部分 -->
    <header class="header">
      <h1>todos</h1>
      <input
        v-model="addName"
        class="new-todo"
        placeholder="请输入任务名称"
        autofocus
        @keydown.enter="add"
        @click="clickAll(1,$event)"
      />
    </header>

    <!-- 主体部分 -->
    <section class="main">
      <input id="toggle-all" 
        class="toggle-all"
        type="checkbox" 
        :checked="newALLSelected"
        @click="clickAll($event)"
        />
      <label for="toggle-all">Mark all as complete</label>
      <ul class="todo-list">
        <!-- 当任务已完成，可以给 li 加上 completed 类，会让元素加上删除线 -->
        <li :class="{ completed: i.isDone }" v-for="i in newList" :key="i.id">
          <div class="view" v-if="i.editShow === false">
            <input class="toggle"
             type="checkbox"
              :checked="i.isDone"
               @click="i.isDone = !i.isDone" 
               
               />
            <label @dblclick="dblclickEdit(i.id,$event)" >{{ i.name }}</label>
            <button class="destroy" @click="delList(i.id)"></button>
          </div>
          <!-- 修改页面 -->
          <input  @blur="i.editShow = false" ref="focusIpt"  v-focus v-else class="edit" :value="i.name" @keypress.enter="editName($event,i.id)"/>
        </li>
      </ul>
    </section>

    <!-- 底部部分 -->
    <footer class="footer" v-show="list.length !== 0">
      <span class="todo-count"
        ><strong>{{ finishList }}</strong
        >剩余</span
      >
      <ul class="filters">
        <li>
          <a :class="{ selected: flag === 1 }" @click="sipk(1)">全部</a>
        </li>
        <li>
          <a :class="{ selected: flag === 2 }" @click="sipk(2)">进行中</a>
        </li>
        <li>
          <a :class="{ selected: flag === 3 }" @click="sipk(3)">已完成</a>
        </li>
      </ul>
      <button class="clear-completed" @click="delFinish">清除已完成</button>
    </footer>
  </section>
</template>



<script>
export default {
  data() {
    return {
      list: JSON.parse(localStorage.getItem('todosList')) || [],
      addName: "",
      flag: 1,
    };
  },
  methods: {
    clickAll(e){
      console.log(e.target.value)
      console.dir(e.target.checked)
      this.list.map(i => i.isDone = e.target.checked)
    },
    editName(e,id){
      console.log(e.target.value)
      console.log(id)
    const res =  this.list.find(i => i.id === id)
    res.name =  e.target.value
    res.editShow = false
    },
    add() {
      if (this.addName.trim().length !== 0) {
        this.list.push({
          id: +new Date(),
          name: this.addName,
          isDone: false,
          editShow:false
        });
        this.addName = "";
      }
    },
    delList(id) {
      this.list = this.list.filter((i) => i.id !== id);
    },
    sipk(flag) {
      this.flag = flag;
    },
    delFinish() {
      this.list = this.list.filter((i) => i.isDone === false);
    },
    // 双击修改
    dblclickEdit(id,e){
     const res =  this.list.find(i => i.id === id)
     res.editShow = true
      console.log(e.target.innerText)
     
    },
  
    // blurEvent(id){
    //   console.log(this.list)
    //  const res =  this.list.find(i => i.id === id)
    //     res.editShow = !res.editShow
    // }
  },
  computed: {
    newALLSelected () {
      return  this.list.every(i => i.isDone === true)
    },
    newList() {
      if (this.flag === 2) {
        return this.list.filter((i) => i.isDone === false);
      } else if(this.flag === 3){
        return this.list.filter((i) => i.isDone === true);
      }else return this.list
    },
    finishList(){
      const res =   this.list.filter(i => i.isDone === false)
      return res.length
    },
  },
  directives:{
    focus:{
      inserted(el){
        console.log(el)
          el.focus()
      },
    }
  },
  watch:{
    list:{
      deep:true,
      handler(val){
        localStorage.setItem('todosList',JSON.stringify(val))
      }
    }
  },
};
</script>

